<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import themeConfig from '@/../themeConfig.js'
import AppService from '@/services/commons/AppService'

export default {
  data () {
    return {
      branding: {}
    }
  },
  methods: {
    load () {
      let self = this
      AppService.appBranding().then(function (response) {
        self.branding = response.data.appBranding || {}
        self.$vs.theme({
          primary: self.branding.primaryColor ? self.branding.primaryColor : '#0096C8',
          success: self.branding.successColor ? self.branding.successColor : '#28C76F',
          danger: self.branding.dangerColor ? self.branding.dangerColor : '#EA5455',
          warning: self.branding.warningColor ? self.branding.warningColor : '#FF9F43',
          dark: self.branding.darkColor ? self.branding.darkColor : '#231F20',
          loginLogoBackgroundColor: self.branding.loginLogoBackgroundColor ? self.branding.loginLogoBackgroundColor : '#FFFFFF',
          title: self.branding.titleColor ? self.branding.titleColor : '#0096C8'
        })
        self.$store.dispatch('setBranding', {
          loginLogoSize: self.branding.loginLogoSize,
          loginBackgroundPosition: self.branding.loginBackgroundPosition,
          loginBackgroundAvailable: self.branding.loginBackgroundAvailable,
          loginLogoAvailable: self.branding.loginLogoAvailable,
          logoAvailable: self.branding.logoAvailable
        })
      })
    },
    toggleClassInBody (className) {
      if (className === 'dark') {
        if (document.body.className.match('theme-semi-dark')) document.body.classList.remove('theme-semi-dark')
        document.body.classList.add('theme-dark')
      } else if (className === 'semi-dark') {
        if (document.body.className.match('theme-dark')) document.body.classList.remove('theme-dark')
        document.body.classList.add('theme-semi-dark')
      } else {
        if (document.body.className.match('theme-dark')) document.body.classList.remove('theme-dark')
        if (document.body.className.match('theme-semi-dark')) document.body.classList.remove('theme-semi-dark')
      }
    }
  },
  mounted () {
    this.load()
    this.toggleClassInBody(themeConfig.theme)
  },
  watch: {
    '$store.state.theme' (val) {
      this.toggleClassInBody(val)
    }
  }
}
</script>
