import Endpoint from '@/services/Endpoint'
import store from '@/store/store'

export default {
  apps (payload) {
    return Endpoint().get('/s1/connect/server/app', {
      params: payload
    })
  },
  appBranding () {
    return Endpoint().get(`/s1/connect/server/app/${store.getters.appId}/branding`, {})
  }
}
