<template>
  <div class="nv-input-group flex">

    <!-- SLOT: PREPEND -->
    <div class="nv-input-group-prepend flex rounded-tl rounded-bl" :class="classesPrepend" v-if="this.$slots.prepend">
      <slot name="prepend"></slot>
    </div>

    <!-- SLOT: DEFAULT -->
    <div class="nv-input-group-default flex-grow">
      <slot></slot>
    </div>

    <!-- SLOT: APPEND -->
    <div class="nv-input-group-append flex rounded-tr rounded-br" :class="classesAppend" v-if="this.$slots.append">
      <slot name="append"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'nv-input-group',
  props: {
    prependClasses: {
      type: String
    },
    appendClasses: {
      type: String
    }
  },
  computed: {
    classesPrepend () {
      return `${this.prependClasses}`
    },
    classesAppend () {
      return `${this.appendClasses}`
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/vuesax/components/nvInputGroup.scss";
</style>
