import axios from 'axios'
import nprogress from 'nprogress'
import store from '@/store/store'
import router from '@/router/router'

export default () => {
  // create axios instance
  let instance = axios.create({
    baseURL: process.env.VUE_APP_API_BASE_PATH
  })

  // add interceptors
  instance.interceptors.request.use(function (config) {
    nprogress.start()

    // add session token
    if (store.state.session.sessionToken) {
      config.headers.sessionToken = store.state.session.sessionToken
    }

    // add default parameters
    if (config.params) {
      config.params.sourceAppId = store.getters.appId
      if (store.getters.activeExternalId) {
        config.params.customerId = store.getters.activeExternalId.externalId
      }
    } else if (config.data) {
      if (config.data instanceof FormData) {
        config.data.append('sourceAppId', store.getters.appId)
        if (store.getters.activeExternalId) {
          config.data.append('customerId', store.getters.activeExternalId.externalId)
        }
      } else {
        config.data.sourceAppId = store.getters.appId
        if (store.getters.activeExternalId) {
          config.data.customerId = store.getters.activeExternalId.externalId
        }
      }
    }

    // all done
    return config
  })
  instance.interceptors.response.use(function (response) {
    nprogress.done()
    return response
  }, function (error) {
    nprogress.done()
    if (error.response.status === 401) {
      if (router.currentRoute.meta.rule !== 'isPublic') {
        router.push({ name: 'Login' })
      }
    }
    return Promise.reject(error)
  })

  // return instance
  return instance
}
