import SceneService from '@/services/commons/SceneService'

const VueScenePlugin = {
  install (Vue, store, router) {
    router.beforeEach((to, from, next) => {
      SceneService.scene(to.path).then(function (response) {
        let data = response.data
        if (data.scene) {
          SceneService.sceneMessages(store.state.locale.languageCode, data.scene.sceneId).then(function (response) {
            let data = response.data
            if (data.messageList) {
              Vue.i18n.remove(store.state.locale.languageCode)
              Vue.i18n.set(store.state.locale.languageCode)
              for (let i in data.messageList) {
                let message = {}
                message[data.messageList[i].messageKey] = data.messageList[i].messageValue
                Vue.i18n.add(store.state.locale.languageCode, message)
              }
            }
            next()
          })
        } else {
          console.warn(`Scene not found: ${to.path}`)
          next()
        }
      })
    })
  }
}

export default VueScenePlugin
