const getters = {
  appId: state => {
    return process.env.VUE_APP_ID
  },
  activeExternalId: state => {
    return state.user.externalIds[state.externalIdIndex]
  },
  isSessionAuthenticated: state => () => {
    return state.session != null && state.session.sessionToken !== null && state.session.sessionValidity > 0
  }
}

export default getters
