import Endpoint from '@/services/Endpoint'

export default {
  sessionToken () {
    return Endpoint().get('/moquiSessionToken')
  },
  registerUser (payload) {
    return Endpoint().post('/s1/connect/security/user/register', payload)
  },
  loginUser (username, password) {
    let authorization = 'Basic ' + btoa(username + ':' + password)
    return Endpoint().post('/s1/connect/security/user/login', {}, {
      headers: {
        'Authorization': authorization
      }
    })
  },
  logoutUser () {
    return Endpoint().get('/logout')
  },
  poll (payload) {
    return Endpoint().get('/s1/connect/security/user/poll', {
      params: payload
    })
  },
  profile (payload) {
    return Endpoint().get('/s1/connect/security/user/profile', {
      params: payload
    })
  },
  modifyUserProfile (payload) {
    return Endpoint().post('/s1/connect/security/user/profile', payload)
  },
  preference (payload) {
    return Endpoint().get('/s1/connect/security/user/preference', {
      params: payload
    })
  },
  updateUserOtpChannel (payload) {
    return Endpoint().post('/s1/connect/security/user/otpChannel', payload)
  },
  changeUserPassword (payload) {
    return Endpoint().post('/s1/connect/security/user/password/change', payload)
  },
  changeTemporaryUserPassword (payload) {
    return Endpoint().post('/s1/connect/security/user/password/temporary/change', payload)
  },
  resetUserPassword (payload) {
    return Endpoint().post('/s1/connect/security/user/password/reset', payload)
  },
  generateOneTimePassword (payload) {
    return Endpoint().post('/s1/connect/security/user/password/oneTime/generate', payload)
  },
  authenticateUser (payload) {
    return Endpoint().post('/s1/connect/security/user/authenticate', payload)
  },
  updateUserLanguage (payload) {
    return Endpoint().post('/s1/connect/security/user/language', payload)
  },
  users (payload) {
    return Endpoint().get('/s1/connect/security/user/profile', {
      params: payload
    })
  },
  userEvents (payload) {
    return Endpoint().get('/s1/connect/security/user/event', {
      params: payload
    })
  },
  userGroups (payload) {
    return Endpoint().get('/s1/connect/security/userGroup', {
      params: payload
    })
  }
}
