import MainMenu from '@/navigation/MainMenu'
import { themeConfig, colors } from '@/../themeConfig.js'

const getDefaultState = () => {
  return {
    requestPath: null,
    externalIdIndex: localStorage.getItem('externalIdIndex') || null,
    user: {
      userId: localStorage.getItem('userId') || null,
      username: localStorage.getItem('username') || null,
      userFullName: localStorage.getItem('userFullName') || null,
      userRoles: localStorage.getItem('userRoles') || null,
      externalIds: JSON.parse(localStorage.getItem('externalIds')) || []
    },
    locale: {
      languageCode: 'en',
      rtl: false
    },
    session: {
      sessionId: localStorage.getItem('sessionId') || null,
      sessionValidity: localStorage.getItem('sessionValidity') || null,
      sessionToken: localStorage.getItem('sessionToken') || null
    },
    branding: {
      loginLogoSize: localStorage.getItem('loginLogoSize') || null,
      loginBackgroundPosition: localStorage.getItem('loginBackgroundPosition') || null,
      loginBackgroundAvailable: localStorage.getItem('loginBackgroundAvailable') || null,
      loginLogoAvailable: localStorage.getItem('loginLogoAvailable') || null,
      logoAvailable: localStorage.getItem('logoAvailable') || null
    },
    workflowTasks: 0,
    breakpoint: null,
    isSidebarActive: true,
    reduceButton: themeConfig.sidebarCollapsed,
    bodyOverlay: false,
    sidebarWidth: 'default',
    sidebarItemsMin: false,
    theme: themeConfig.theme || 'light',
    themePrimaryColor: colors.primary,
    mainMenu: MainMenu,
    starredPages: MainMenu.data.filter((page) => page.highlightAction)
  }
}

const state = getDefaultState()

export { state as default, getDefaultState }
