import { getDefaultState } from '@/store/state'

const mutations = {
  // ////////////////////////////////////////////
  // SYSTEM
  // ////////////////////////////////////////////

  setUser (state, payload) {
    state.user = payload
    state.externalIdIndex = 0
    localStorage.setItem('userId', state.user.userId)
    localStorage.setItem('username', state.user.username)
    localStorage.setItem('userFullName', state.user.userFullName)
    localStorage.setItem('userRoles', state.user.userRoles)
    localStorage.setItem('externalIds', JSON.stringify(state.user.externalIds))
    localStorage.setItem('externalIdIndex', state.externalIdIndex)
  },
  setUserRoles (state, payload) {
    state.user.userRoles = payload.userRoles
    localStorage.setItem('userRoles', state.user.userRoles)
  },
  setLocale (state, payload) {
    state.locale = payload
    localStorage.setItem('languageCode', state.locale.languageCode)
  },
  setSession (state, payload) {
    state.session.sessionId = payload.sessionId || state.session.sessionId
    state.session.sessionValidity = payload.sessionValidity || state.session.sessionValidity
    state.session.sessionToken = payload.sessionToken || state.session.sessionToken
    localStorage.setItem('sessionId', state.session.sessionId)
    localStorage.setItem('sessionValidity', state.session.sessionValidity)
    localStorage.setItem('sessionToken', state.session.sessionToken)
  },
  decrementSessionValidity (state, payload) {
    state.session.sessionValidity = state.session.sessionValidity - 1
    localStorage.setItem('sessionValidity', state.session.sessionValidity)
  },
  setWorkflowTasks (state, payload) {
    state.workflowTasks = payload.workflowTasks
    localStorage.setItem('workflowTasks', state.workflowTasks)
  },
  setRequestPath (state, requestPath) {
    state.requestPath = requestPath
  },
  setExternalIdIndex (state, idIndex) {
    state.externalIdIndex = idIndex
    localStorage.setItem('externalIdIndex', state.externalIdIndex)
  },
  resetState (state) {
    localStorage.clear()
    let defaultState = getDefaultState()
    state.user = defaultState.user
    state.session = defaultState.session
    state.locale = defaultState.locale
  },
  setBranding (state, branding) {
    state.branding = branding
    localStorage.setItem('loginLogoSize', state.branding.loginLogoSize)
    localStorage.setItem('loginBackgroundPosition', state.branding.loginBackgroundPosition)
    localStorage.setItem('loginBackgroundAvailable', state.branding.loginBackgroundAvailable)
    localStorage.setItem('loginLogoAvailable', state.branding.loginLogoAvailable)
    localStorage.setItem('logoAvailable', state.branding.logoAvailable)
  },
  // ////////////////////////////////////////////
  // SIDEBAR & UI UX
  // ////////////////////////////////////////////

  UPDATE_SIDEBAR_WIDTH (state, width) {
    state.sidebarWidth = width
  },
  UPDATE_SIDEBAR_ITEMS_MIN (state, val) {
    state.sidebarItemsMin = val
  },
  TOGGLE_REDUCE_BUTTON (state, val) {
    state.reduceButton = val
  },
  TOGGLE_CONTENT_OVERLAY (state, val) {
    state.bodyOverlay = val
  },
  TOGGLE_IS_SIDEBAR_ACTIVE (state, value) {
    state.isSidebarActive = value
  },
  UPDATE_THEME (state, val) {
    state.theme = val
  },
  UPDATE_WINDOW_BREAKPOINT (state, val) {
    state.breakpoint = val
  },
  UPDATE_PRIMARY_COLOR (state, val) {
    state.themePrimaryColor = val
  },

  // ////////////////////////////////////////////
  // COMPONENT
  // ////////////////////////////////////////////

  // NvAutoSuggest
  UPDATE_STARRED_PAGE (state, payload) {
    // find item index in search list state
    const index = state.mainMenu.data.findIndex((item) => item.index === payload.index)
    // update the main list
    state.mainMenu.data[index].highlightAction = payload.val

    // if val is true add it to starred else remove
    if (payload.val) {
      state.starredPages.push(state.mainMenu.data[index])
    } else {
      // find item index from starred pages
      const index = state.starredPages.findIndex((item) => item.index === payload.index)
      // remove item using index
      state.starredPages.splice(index, 1)
    }
  },

  // The Navbar
  ARRANGE_STARRED_PAGES_LIMITED (state, list) {
    const starredPagesMore = state.starredPages.slice(10)
    state.starredPages = list.concat(starredPagesMore)
  },
  ARRANGE_STARRED_PAGES_MORE (state, list) {
    let downToUp = false
    let lastItemInStarredLimited = state.starredPages[10]
    const starredPagesLimited = state.starredPages.slice(0, 10)
    state.starredPages = starredPagesLimited.concat(list)

    state.starredPages.slice(0, 10).map((i) => {
      if (list.indexOf(i) > -1) downToUp = true
    })
    if (!downToUp) {
      state.starredPages.splice(10, 0, lastItemInStarredLimited)
    }
  }
}

export default mutations
