import Endpoint from '@/services/Endpoint'

export default {
  scene (lookupName) {
    return Endpoint().get('/s1/connect/server/scene', {
      params: {
        lookupName: lookupName
      }
    })
  },
  sceneMessages (locale, sceneId) {
    return Endpoint().get(`/s1/connect/server/scene/${sceneId}/message`, {
      params: {
        locale: locale
      }
    })
  }
}
