import Vue from 'vue'

const actions = {
  // ////////////////////////////////////////////
  // SYSTEM
  // ////////////////////////////////////////////

  setUser ({ commit }, payload) {
    commit('setUser', payload)
  },
  setUserRoles ({ commit }, payload) {
    commit('setUserRoles', payload)
  },
  setLocale ({ commit }, payload) {
    commit('setLocale', payload)
    Vue.i18n.set(payload.languageCode)
  },
  setSession ({ commit }, payload) {
    commit('setSession', payload)
  },
  decrementSessionValidity ({ commit }, payload) {
    commit('decrementSessionValidity', payload)
  },
  setWorkflowTasks ({ commit }, payload) {
    commit('setWorkflowTasks', payload)
  },
  setRequestPath ({ commit }, payload) {
    commit('setRequestPath', payload.requestPath)
  },
  setExternalIdIndex ({ commit }, payload) {
    commit('setExternalIdIndex', payload)
  },
  resetState ({ commit }, payload) {
    commit('resetState')
  },
  setBranding ({ commit }, payload) {
    commit('setBranding', payload)
  },
  // ////////////////////////////////////////////
  // SIDEBAR & UI UX
  // ////////////////////////////////////////////

  updateSidebarWidth ({ commit }, width) {
    commit('UPDATE_SIDEBAR_WIDTH', width)
  },
  updateI18nLocale ({ commit }, locale) {
    commit('UPDATE_I18N_LOCALE', locale)
  },
  toggleContentOverlay ({ commit }) {
    commit('TOGGLE_CONTENT_OVERLAY')
  },
  updateTheme ({ commit }, val) {
    commit('UPDATE_THEME', val)
  },

  // ////////////////////////////////////////////
  // COMPONENT
  // ////////////////////////////////////////////

  // NvAutoSuggest
  updateStarredPage ({ commit }, payload) {
    commit('UPDATE_STARRED_PAGE', payload)
  },

  //  The Navbar
  arrangeStarredPagesLimited ({ commit }, list) {
    commit('ARRANGE_STARRED_PAGES_LIMITED', list)
  },
  arrangeStarredPagesMore ({ commit }, list) {
    commit('ARRANGE_STARRED_PAGES_MORE', list)
  }
}

export default actions
