// Vue plugins
import '@babel/polyfill'
import Vue from 'vue'
import VuexI18n from 'vuex-i18n'
import VueEventHub from 'vue-event-hub'
import App from '@/App.vue'
import Vuesax from 'vuesax' // Vuesax Component Framework
import router from '@/router/router' // Vue Router
import store from '@/store/store' // Vuex Store
import filters from './filters'
import VueScenePlugin from '@/plugins/vue-scene'
import NvTooltip from '@/components/nv-tooltip/NvTooltip.vue'
import NvCard from '@/components/nv-card/NvCard.vue'
import NvList from '@/components/nv-list/NvList.vue'
import NvBreadcrumb from '@/components/nv-breadcrumb/NvBreadcrumb.vue'
import NvInputGroup from '@/components/nv-input-group/NvInputGroup.vue'
import FeatherIcon from '@/components/feather-icon/FeatherIcon.vue'
import VueCodemirror from 'vue-codemirror'
import Vue2Filters from 'vue2-filters'
import VueTimers from 'vue-timers'
import acl from '@/acl'
import * as VueGoogleMaps from 'vue2-google-maps'
import { AgGridEnterprise, LicenseManager } from 'ag-grid-enterprise'
import cssVars from 'css-vars-ponyfill'
import ieFixes from '@/assets/js/ie-fixes.js'

// Js imports
import '@/../themeConfig.js'// Theme Configurations
import 'codemirror/mode/vue/vue.js'
import 'codemirror/mode/css/css.js'
import 'codemirror/addon/selection/active-line.js'
import 'codemirror/addon/edit/closebrackets.js'
import 'codemirror/addon/edit/closetag.js'
import 'codemirror/addon/edit/matchtags.js'
import 'codemirror/addon/display/autorefresh.js'

// Styles: SCSS
import '@fortawesome/fontawesome-pro/css/all.min.css'
import 'material-icons/iconfont/material-icons.css' // Material Icons
import 'vuesax/dist/vuesax.css'
import '@/assets/css/iconfont.css' // Feather font icon
import 'nprogress/nprogress.css'
import 'vue-simple-suggest/dist/styles.css'
import 'vue-tree-halower/dist/halower-tree.min.css'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/eclipse.css'
import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-balham.css'
import 'markdown-it-vue/dist/markdown-it-vue.css'
import '@/assets/scss/main.scss'
import '@/assets/css/main.css' // Tailwind

Vue.use(VuexI18n.plugin, store)
Vue.use(VueScenePlugin, store, router)
Vue.use(VueEventHub)
Vue.use(Vuesax)
Vue.use(filters)
Vue.use(VueCodemirror)
Vue.use(Vue2Filters)
Vue.use(VueTimers)
Vue.component(NvTooltip.name, NvTooltip)
Vue.component(NvCard.name, NvCard)
Vue.component(NvList.name, NvList)
Vue.component(NvBreadcrumb.name, NvBreadcrumb)
Vue.component(NvInputGroup.name, NvInputGroup)
Vue.component(FeatherIcon.name, FeatherIcon)
Vue.component(AgGridEnterprise)

Vue.use(VueGoogleMaps, {
  load: {
    key: process.env.VUE_APP_GOOGLE_MAP_API_KEY,
    libraries: 'places'
  }
})

// ag-grid enterprise license key
LicenseManager.setLicenseKey(process.env.VUE_APP_AG_GRID_ENTERPRISE_KEY)

// disable Vue production tip
Vue.config.productionTip = false

// JavaScript globals
window.$ = require('jquery')
window.jQuery = require('jquery')
window.draw2d = require('draw2d')

// IE fixes
cssVars({
})
ieFixes.ieFixes()

new Vue({
  router,
  store,
  acl,
  render: h => h(App)
}).$mount('#app')
